<template>
  <div>
    <div class="loginBox">
      <div
        style="
          text-align: center;
          font-size: 30px;
          font-family: cursive;
          margin-bottom: 20px;
        "
      >
        登录
      </div>
      <el-form
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="账号" prop="username">
          <el-input
            v-model="loginForm.username"
            clearable
            placeholder="请输入账号"
          ></el-input>
        </el-form-item>
        <el-form-item label="密码" prop="password">
          <el-input
            v-model="loginForm.password"
            clearable
            placeholder="请输入密码"
          ></el-input>
        </el-form-item>
        <div style="text-align: center">
          <el-button
            type="primary"
            :loading="loginLoading"
            @click="submitForm('loginForm', 1)"
            >登录</el-button
          >
          <el-button
            type="primary"
            :loading="checkLoading"
            @click="submitForm('loginForm', 2)"
            >注册</el-button
          >
          <el-button @click="resetForm('loginForm')">重置</el-button>
          <el-button type="success" @click="goBoosterPool()">助力池</el-button>
        </div>
      </el-form>
    </div>
  </div>
</template>
<script>
import { Message } from "element-ui";
import { login, check } from "../axiosRequest/allRequest";

export default {
  data() {
    return {
      loginForm: {
        username: "",
        password: "",
      },
      loginRules: {
        username: [{ required: true, message: "请输入账号", trigger: "blur" }],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      loginLoading: false,
      checkLoading: false,
    };
  },
  created() {
    // window.reload();
  },
  methods: {
    goBoosterPool() {
      this.$router.push("/help");
    },
    submitForm(formName, type) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if (type == 1) {
            this.loginLoading = true;
            login(this.loginForm).then((res) => {
              this.loginLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                sessionStorage.setItem("robotToken", res.data.token);
                Message.success("登录成功！");
                this.$router.replace("/MainPage");
              }
            });
          } else if (type == 2) {
            this.checkLoading = true;
            check(this.loginForm).then((res) => {
              this.checkLoading = false;
              if (res.code != 200) {
                Message.error(res.message);
              } else {
                Message.success("注册成功！");
              }
            });
          }
        } else {
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
};
</script>

<style lang="less" scoped>
.loginBox {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 30%;
  height: 30vh;
  min-height: 249px;
  min-width: 420px;
  // aspect-ratio: 2/1;
  margin: auto;
  border-radius: 5px;
  box-shadow: 2px 2px 5px #888888;
  background-color: rgba(111, 222, 123, 0.1);
  padding: 10px;
}

.demo-ruleForm {
  padding-right: 20px;
}
</style>
